@import "variables/variables";

@font-face {
    font-family: "#{$icomoon-font-family}";
    src: url("#{$icomoon-font-path}/#{$icomoon-font-family}.eot?4r1frz");
    src: url("#{$icomoon-font-path}/#{$icomoon-font-family}.eot?4r1frz#iefix") format("embedded-opentype"),
        url("#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?4r1frz") format("truetype"),
        url("#{$icomoon-font-path}/#{$icomoon-font-family}.woff?4r1frz") format("woff"),
        url("#{$icomoon-font-path}/#{$icomoon-font-family}.svg?4r1frz##{$icomoon-font-family}") format("svg");
    font-weight: normal;
    font-style: normal;
    font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: "#{$icomoon-font-family}" !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

